<template>
  <div class="managerPanoramic">
    <div class="top">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>关注标签</span>
          </div>
          <div class="chart-main">
            <themeLabelList
              :labelData="labelData"
              :pageConfig="labelPageConfig"
              @changePage="changePage"
              :showDetailData="labelDetailData"
              @clickDetail="getLabelDetailData"
              @changeDetailPage="changePage"
              @exportExcel="themeLabelExportExcel"
            ></themeLabelList>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>客户分群</span>
          </div>
          <div class="chart-main">
            <div class="main-left">
              <div class="choose-box">
                <div class="box-title">
                  <div class="text">可选标签列表</div>
                  <div class="count">已选择{{ clientGroupDataChoose.length || 0 }}项</div>
                </div>
                <div class="box-content">
                  <div class="content-item" v-for="(item, index) in clientGroupData" :key="index">
                    <el-checkbox class="checkbox" v-model="item.checked"></el-checkbox>
                    <span class="text">{{ item.showText }}</span>
                    <span class="value">{{'(' + item.value + '户)' }}</span>
                  </div>
                </div>
              </div>
              <div class="choose-condition">
                <el-select
                  v-model="conditionType"
                  size="mini"
                  class="input"
                  placeholder
                  @change="getFilterResult"
                >
                  <el-option
                    v-for="item in conditionTypeOptions"
                    :key="item.value"
                    :label="item.name"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </div>
            </div>
            <div class="main-right">
              <div class="choose-box">
                <div class="box-title">
                  <div class="text">已选标签列表</div>
                </div>
                <div class="box-content">
                  <div
                    class="content-item"
                    v-for="(item, index) in clientGroupDataChoose"
                    :key="index"
                  >
                    <span class="text">{{ item.showText }}</span>
                    <span class="value">{{'(' + item.value + '户)' }}</span>
                  </div>
                </div>
              </div>
              <div class="choose-result">
                <div class="box-title">
                  <div class="text">客户数</div>
                </div>
                <div class="box-content">{{ clientGroupResult }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="top-bottom">
      <div class="content content-border">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span>台区覆盖率</span>
          </div>
          <div class="content-title-right">
            <em
              @click="changeChartAndList('customerCoverageActive','1')"
              :class="customerCoverageActive==='1'?'active':'' "
              class="icon-style iconfont icon-tubiao"
            ></em>
            <em
              @click="changeChartAndList('customerCoverageActive','2')"
              :class="customerCoverageActive==='2'?'active':'' "
              class="icon-style iconfont icon-biaoge"
            ></em>
            <!-- <em
              @click="changeChartAndList('customerCoverageActive','3')"
              :class="customerCoverageActive==='3'?'active':'' "
              class="icon-style iconfont icon-relitu"
            ></em>-->
          </div>
        </div>
        <div class="chart-main">
          <barCharts
            v-if="customerCoverageRes &&customerCoverageActive==='1'"
            :barChartsData="customerCoverageData"
            :barWidth="40"
            :barType="'bar'"
            :isShadow="true"
            @clickBar="getCustomerCoverageBarData"
            :showDetail="true"
            :showDetailData="customerCoverageBarData"
            @changePageData="changePage"
            :name="'台区覆盖率'"
            @setOption="setOption"
            @exportExcel="detailListExportExcel"
            :maxShowNumber="13"
          ></barCharts>
          <div class="search" v-if="customerCoverageActive==='2'">
            <el-input
              style="width: 300px"
              v-model="customerCoverageQueryForm.name"
              size="mini"
              placeholder="请输入"
            ></el-input>
            <el-button type="primary" size="mini" @click="getCustomerCoverageDetailData">搜索</el-button>
          </div>
          <bdaTable
            v-if="customerCoverageActive==='2'"
            class="content-table"
            :tableData="customerCoverageDetailData"
            :headerColor="headerColor"
            :isPage="true"
            :pageConfig="customerCoveragePageConfig"
            @changePage="changePage"
          ></bdaTable>
        </div>
      </div>
    </div>

    <div class="center">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span class="text">客户数</span>
          </div>
          <div class="chart-main">
            <pieCharts
              v-if="clientUsersRes"
              :pieChartsData="clientUsersData"
              :roseType="false"
              :radius="['0%', '70%']"
              :labelType="'emphasis'"
              :legendShow="true"
              :titleShow="true"
              :name="'客户数'"
              @setOption="setOption"
              :totalData="clientUsersTotlData"
            ></pieCharts>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>网格热点分布</span>
          </div>
          <div class="chart-main">
            <wordCloud
              v-if="hotWordRes"
              :wordCloudData="hotWordData"
              :name="'热词分布'"
              @setOption="setOption"
            ></wordCloud>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span
              class="change-button"
              :class="dataType === 'counts' ? 'active' : ''"
              @click="changeCourts('counts')"
            >台区信息</span>
            <span
              class="change-button"
              :class="dataType === 'line' ? 'active' : ''"
              @click="changeCourts('line')"
            >线路信息</span>
          </div>
          <div class="chart-main" v-if="dataType === 'counts'">
            <bdaTable
              class="main-table"
              :tableData="courtsData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="courtsPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
          <div class="chart-main" v-if="dataType === 'line'">
            <bdaTable
              class="main-table"
              :tableData="lineData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="linePageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>委派任务办理情况</span>
          </div>
          <div class="chart-main">
            <bdaTable
              class="main-table"
              :tableData="handlingSituationData"
              :headerColor="headerColor"
              :isPage="true"
              :pageConfig="handlingSituationPageConfig"
              @changePage="changePage"
            ></bdaTable>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/panoramic-application';
import bdaTable from '@/components/bda-table/bda-table'
import pieCharts from '@/components/pie-charts/pie-charts'
import barCharts from '@/components/bar-charts/bar-charts';
import wordCloud from '@/components/word-cloud/word-cloud'
import themeLabelList from '@/components/theme-label-list/theme-label-list'
import { exportExcelGet, exportExcelPost } from "@/utils/export";

export default {
  components: {
    barCharts,
    bdaTable,
    pieCharts,
    wordCloud,
    themeLabelList
  },
  name: "managerPanoramic",
  mixins: [Interface],
  data() {
    return {
      //表头颜色
      headerColor: '#1899fa',
      //关注标签
      labelData: [],

      labelQueryForm: {
        current: 1,
        size: 10,
      },
      //分页设置
      labelPageConfig: {
        name: "label",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //标签详情
      labelDetailData: {},
      labelDetailQueryForm: {
        dbcolumnAS: "",
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      labelDetailPageConfig: {
        name: "labelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //客户数
      clientUsersRes: [],
      clientUsersData: {},
      clientUsersTotlData: 0,
      //客户分群
      clientGroupData: [],
      clientGroupDataChoose: [],
      conditionType: '',
      conditionTypeOptions: '',
      clientGroupResult: 0,
      //台区客户覆盖户数-图
      customerCoverageActive: '1',
      customerCoverageData: [],
      customerCoverageRes: false,
      //台区客户覆盖户数-表
      customerCoverageQueryForm: {
        name: "",
        logic: "",
        condition: [],
        current: 1,
        size: 10,
      },
      //分页设置
      customerCoveragePageConfig: {
        name: "customerCoverage",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //详情数据
      customerCoverageDetailData: {},
      //点击柱状图数据
      customerCoverageBarData: {},
      //台区客户覆盖户数-表
      customerCoverageBarQueryForm: {
        customerNumbers: [],
        courtsNumber: "",
        current: 1,
        size: 10,
      },
      //分页设置
      customerCoverageBarPageConfig: {
        name: "customerCoverageBar",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //热词分布
      hotWordData: {},
      hotWordRes: false,
      //台区信息
      courtsData: {},
      dataType: 'counts',
      courtsQueryForm: {
        current: 1,
        size: 10,
        total: 0,
      },
      //分页设置
      courtsPageConfig: {
        name: "courts",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //线路信息
      lineData: {},
      lineQueryForm: {
        current: 1,
        size: 10,
        total: 0,
      },
      //分页设置
      linePageConfig: {
        name: "line",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //委派任务办理情况
      handlingSituationData: {},
      handlingSituationQueryForm: {
        current: 1,
        size: 10,
        total: 0,
      },
      //分页设置
      handlingSituationPageConfig: {
        name: "handlingSituation",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //图表组件数据
      setOptionData: {},
    }
  },
  watch: {
    clientGroupData: {
      handler() {
        this.clientGroupDataChoose = this.clientGroupData.filter((e) => {
          return e.checked;
        })
      },
      deep: true
    }
  },
  mounted() {
    this.getLabelData()
    this.getClientGroupData()
    this.getHandlingSituationData()
    this.getCustomerCoverageData()
    this.getConditionTypeOptions()
    this.getClientUsersData()
    this.getHotWordData()
    this.getCourtsData()
    window.addEventListener("resize", () => {
      if (this.setOptionData['台区覆盖率']) this.setOptionData['台区覆盖率'].resize();
      if (this.setOptionData['客户数']) this.setOptionData['客户数'].resize();
      if (this.setOptionData['热词分布']) this.setOptionData['热词分布'].resize();
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val
    },
    //切换台区和线路信息
    changeCourts(data) {
      this.dataType = data
      if (data === 'counts') {
        this.getCourtsData()
      }
      if (data === 'line') {
        this.getLineData()
      }

      console.log(this.dataType, 'this.dataType')
    },
    //改变图表或列表展示
    changeChartAndList(type, val) {
      if (type === 'customerCoverageActive') {
        this.customerCoverageActive = val
        if (val === '1') {
          this.customerCoverageQueryForm.name = ''
          this.customerCoverageQueryForm.current = 1
          this.customerCoverageQueryForm.size = 10
          this.getCustomerCoverageData()
        }
        if (val === '2') {
          this.getCustomerCoverageDetailData()
        }
      }
    },
    //改变分页
    changePage(data) {
      if (data.name === 'label') {
        this.labelQueryForm.size = data.size
        this.labelQueryForm.current = data.current
        this.getLabelData()
      }
      if (data.name === 'labelDetail') {
        this.labelDetailQueryForm.dbcolumnAS = data.keyValue || ''
        this.labelDetailQueryForm.size = data.size
        this.labelDetailQueryForm.current = data.current
        this.getLabelDetailData()
      }
      if (data.name === 'customerCoverage') {
        this.customerCoverageQueryForm.size = data.size
        this.customerCoverageQueryForm.current = data.current
        this.getCustomerCoverageDetailData()
      }
      if (data.name === 'customerCoverageBar') {
        this.customerCoverageBarQueryForm.size = data.size
        this.customerCoverageBarQueryForm.current = data.current
        this.getCustomerCoverageBarData()
      }

      if (data.name === 'courts') {
        this.courtsQueryForm.size = data.size
        this.courtsQueryForm.current = data.current
        this.getCourtsData()
      }
      if (data.name === 'line') {
        this.lineQueryForm.size = data.size
        this.lineQueryForm.current = data.current
        this.getLineData()
      }
      if (data.name === 'handlingSituation') {
        this.handlingSituationQueryForm.size = data.size
        this.handlingSituationQueryForm.current = data.current
        this.getHandlingSituationData()
      }
    },
    //主题标签导出
    themeLabelExportExcel(data) {
      exportExcelGet("/gridManagerPanoramaManagerController/exportUserDetailsToTags" + "?tagName=" + this.labelDetailQueryForm.tagName);
    },
    //客户明细列表导出
    detailListExportExcel() {
      console.log('111')
      exportExcelPost("/gridManagerPanoramaManagerController/exportCourtsExcel", {
        courtsNumber: this.customerCoverageBarQueryForm.courtsNumber,
        customerNumbers: this.customerCoverageBarQueryForm.customerNumbers,
      });

    },
    // 获取关注标签
    getLabelData() {

      this.payAttentionTags(this.labelQueryForm).then(res => {
        this.labelPageConfig.total = res.total
        this.labelPageConfig.size = this.labelQueryForm.size
        this.labelPageConfig.current = this.labelQueryForm.current
        this.labelData = res.list.map(e => {
          return {
            ...e,
            name: e.bqmc,
            text: e.bqsm,
            value: e.sl || 0,
          }
        })

        console.log(this.labelData, 'labelData')

      })
    },
    //获取标签详情信息
    getLabelDetailData(data) {
      if (data) {
        this.labelDetailQueryForm.tagName = data.name
        this.labelDetailQueryForm.size = 10
        this.labelDetailQueryForm.current = 1
      }
      let columnNameData = [
        { prop: "yhbh", label: "用户编号" },
        { prop: "khbh", label: "客户编号" },
        { prop: "yhmc", label: "用户名称" },
        { prop: "yddz", label: "用电地址" },
        { prop: "yhlb", label: "用户类别" },
        { prop: "ydlb", label: "用电类别" },
        { prop: "sfzdgz", label: "是否重点关注客户" },
        { prop: "sfxh", label: "是否现货客户" },
        { prop: "dq", label: "地区" },
        { prop: "qxj", label: "县区局" },
        { prop: "gddw", label: "供电单位" },
        { prop: "cjsj", label: "创建时间" },
        { prop: "gxsj", label: "更新时间" },
      ]
      this.userDetailsToTags({ ... this.labelDetailQueryForm }).then(res => {
        this.labelDetailPageConfig.total = res.total
        this.labelDetailPageConfig.size = this.labelDetailQueryForm.size
        this.labelDetailPageConfig.current = this.labelDetailQueryForm.current
        this.labelDetailData = {
          tableData: [...res.list],
          columnNameData: columnNameData,
          name: "labelDetail",
          headerColor: '#1899fa',
          title: "客户明细列表",
          total: this.labelDetailPageConfig.total,
          current: this.labelDetailQueryForm.current,
          size: this.labelDetailQueryForm.size,

        }
        console.log(this.labelDetailData, 'labelDetailData')
      })
    },
    // 获取客户数
    getClientUsersData() {
      this.clientUsersRes = false
      this.numberOfCustomer().then(res => {
        console.log(res, 'res')
        if (res.totalCustomers !== 0) {

          this.clientUsersData = {
            dataMap: [
              {
                name: '重点关注客户数',
                value: res.focusOnCustomers
              },
              {
                name: '现货客户数',
                value: res.spotGoodsCustomers
              },
            ],
            colorMap: [
              '#E7BB1D',
              '#3B9AFF',
            ],
          }
          this.clientUsersTotlData = res.totalCustomers
          this.clientUsersRes = true
        }
      })

    },
    //客户分群过滤
    getFilterResult() {
      let data = []
      this.clientGroupDataChoose.forEach(e => {
        data.push(e.showText)
      })
      this.customerCoverageQueryForm.logic = this.conditionType
      this.customerCoverageQueryForm.condition = data
      if (this.customerCoverageQueryForm.condition.length !== 0) {
        this.customerClusteringStatistics({
          logic: this.customerCoverageQueryForm.logic,
          condition: this.customerCoverageQueryForm.condition
        }).then(res => {
          this.clientGroupResult = res
          this.getCustomerCoverageData()
        })
      }

    },
    // 客户分群可选列表
    getClientGroupData() {
      this.customerSegmentation().then(res => {
        console.log(res, 'customerSegmentation')
        this.clientGroupData = res.map(e => {
          return {
            ...e,
            showText: e.BQMC,
            value: e.NUM,
            checked: false,
          }
        })
      })
    },
    // 台区客户覆盖户数
    getCustomerCoverageData() {
      this.customerCoverageRes = false
      this.courtsCustomerCoveringNumberGraph(this.customerCoverageQueryForm).then(res => {
        console.log(res, 'courtsCustomerCoveringNumberGraph')
        let dataMap = []
        let xNameData = []
        res.forEach(e => {
          dataMap.push(
            { value: e.khs, name: e.tqmc, number: e.yhbh, id: e.tqbs }
          )
          xNameData.push(e.tqmc)
        })
        this.customerCoverageData = {
          dataMap: {
            '台区客户覆盖户数': dataMap
          },
          xNameData: xNameData,
          colorMap: {
            '台区客户覆盖户数': [
              ['#2790FF', '#55BFFF', '#dcf1ff']
            ]
          },
          xyName: { 'x轴': { name: '台区', color: "#4D4D4D", diffcolor: false }, 'y轴': [{ name: '客户数', color: '#808080' }] }
        }
        this.customerCoverageRes = true
      })
    },
    // 台区客户覆盖户数详情
    getCustomerCoverageDetailData() {
      let columnNameData = [
        { prop: "tqbs", label: "台区标识" },
        { prop: "tqmc", label: "台区数" },
        { prop: "khs", label: "客户数" },
      ]
      // if (this.customerCoverageQueryForm.name) {
      this.courtsCustomerCoveringNumberList(
        {
          ...this.customerCoverageQueryForm,
          courtsNameOrIdentification: this.customerCoverageQueryForm.name
        }).then(res => {
          this.customerCoveragePageConfig.total = res.total
          this.customerCoveragePageConfig.size = this.customerCoverageQueryForm.size
          this.customerCoveragePageConfig.current = this.customerCoverageQueryForm.current
          this.customerCoverageDetailData = {
            tableData: [...res.records],
            columnNameData: columnNameData
          }
        })
      // }
    },
    // 台区客户覆盖户数柱状图详情
    getCustomerCoverageBarData(data) {
      console.log(data, 'getCustomerCoverageBarData')
      if (data) {
        this.customerCoverageBarQueryForm.customerNumbers = data.data.number
        this.customerCoverageBarQueryForm.courtsNumber = data.data.id
        this.customerCoverageBarQueryForm.size = 10
        this.customerCoverageBarQueryForm.current = 1
      }
      let columnNameData = [
        { prop: "khbh", label: "客户编号" },
        { prop: "khmc", label: "客户名称" },
        { prop: "yddz", label: "用电地址" },
        { prop: "yhlb", label: "用户类型" },
        { prop: "ydlb", label: "用电类型" },
        { prop: "dq", label: "地区" },
        { prop: "gddw", label: "供电单位" },

      ]

      this.courtsCustomerCoveringNumberDetails({
        ...this.customerCoverageBarQueryForm,
        customerNumbers: this.customerCoverageBarQueryForm.customerNumbers,
        courtsNumber: this.customerCoverageBarQueryForm.courtsNumber,
      }).then(res => {
        this.customerCoverageBarPageConfig.total = res.total
        this.customerCoverageBarPageConfig.size = this.customerCoverageBarQueryForm.size
        this.customerCoverageBarPageConfig.current = this.customerCoverageBarQueryForm.current
        this.customerCoverageBarData = {
          tableData: [...res.records],
          headerColor: '#1899fa',
          title: "客户分群客户明细列表",
          name: this.customerCoverageBarPageConfig.name,
          size: this.customerCoverageBarQueryForm.size,
          current: this.customerCoverageBarQueryForm.current,
          total: this.customerCoverageBarPageConfig.total,
          columnNameData: columnNameData
        }
      })
    },
    //获取热词
    getHotWordData() {
      this.hotWordRes = false
      this.gridHotSpotDistribution({}).then(res => {
        this.hotWordData = res.map(e => {
          return {
            name: e.name,
            valueShow: e.count,
          }
        })
        this.hotWordRes = true
        console.log(this.hotWordData, 'hotWordData')
      })
    },
    //获取台区信息
    getCourtsData() {
      this.headerColor = '#1899fa'
      let columnNameData = [
        { prop: "tqbs", label: "台区编号" },
        { prop: "tqmc", label: "台区名称" },
        { prop: "khs", label: "用户数" },
      ]
      this.areaInformation({ ...this.courtsQueryForm }).then(res => {
        this.courtsPageConfig.total = res.total
        this.courtsPageConfig.size = this.courtsQueryForm.size
        this.courtsPageConfig.current = this.courtsQueryForm.current
        this.courtsData = {
          tableData: [
            ...res.records
          ],
          columnNameData: columnNameData
        }
      }).catch(err => {
        if (err) {
          this.courtsData = {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })


    },
    // 获取线路信息
    getLineData() {
      this.headerColor = '#1899fa'
      let columnNameData = [
        { prop: "xlbs", label: "线路编号" },
        { prop: "xlmc", label: "线路名称" },
        { prop: "khs", label: "客户数" },
      ]


      this.lineInformation({ ...this.lineQueryForm }).then(res => {
        this.linePageConfig.total = res.total
        this.linePageConfig.size = this.lineQueryForm.size
        this.linePageConfig.current = this.lineQueryForm.current
        this.lineData = {
          tableData: [
            ...res.records
          ],
          columnNameData: columnNameData
        }
      }).catch(err => {
        if (err) {
          this.courtsData = {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })


    },
    // 获取委派任务办理情况
    getHandlingSituationData() {
      let columnNameData = [
        { prop: "rwlx", label: "任务类型" },
        { prop: "psrwcs", label: "派送任务次数" },
        { prop: "wcrwcs", label: "完成任务次数" },
      ]
      this.handlingOfAssignedTasks(this.handlingSituationQueryForm).then(res => {
        this.headerColor = '#1899fa'
        this.handlingSituationPageConfig.total = res.total
        this.handlingSituationPageConfig.size = this.handlingSituationQueryForm.size
        this.handlingSituationPageConfig.current = this.handlingSituationQueryForm.current
        this.handlingSituationData = {
          tableData: [
            ...res.records
          ],
          columnNameData: columnNameData
        }
      })

    },
    //获取客户分群选项
    getConditionTypeOptions() {
      this.conditionTypeOptions = [
        { name: '且', value: '1' },
        { name: '或', value: '2' },
        { name: '非', value: '3' },
      ]
    }
  },
}
</script>

